.bonus__type { font-size: 11px; font-weight: 900; color: #214ea2; text-transform: uppercase; margin-bottom: 10px; }
.bonus h2 { font-size: 21px; font-weight: 900; color: #212121; line-height: 1.2; margin-bottom: 10px; }
.bonus p { font-size: 10px; color: #868686; line-height: 1.1; margin-bottom: 18px; }
.bonus .bonus__button { padding: 0 23px; }

.challenge { background: url(../img/bg3.jpg) no-repeat 0 0; background-size: cover; min-height: 434px; box-sizing: border-box; padding: 93px 30px 0 430px; display: block; clear: both; }
.challenge__type { font-size: 11px; font-weight: 900; color: #214ea2; text-transform: uppercase; margin-bottom: 10px; }
.challenge__sum { font-size: 70px; font-weight: 900; color: #212121; }
.challenge p { font-size: 10px; color: #868686; line-height: 1.1; margin-bottom: 18px; }
.challenge .challenge__button { margin-right: 10px; }

.support24 { background: #f4f5f7; }
.support24 .in { height: 110px; }
.support24__img { background: url(../img/247.png) no-repeat center; width: 141px; height: 110px; position: absolute; bottom: 0; left: 33px; }
.support24__text { position: absolute; left: 320px; width: 210px; text-align: center; }
.support24__text h3 { font-size: 27px; font-weight: 900; color: #212121; padding-top: 21px; }
.support24__text p { font-size: 12px; color: #b7b7b8; }
.support24__buttons { position: absolute; right: 31px; top: 40px; }
.support24__buttons a { min-width: 110px; text-align: center; margin-left: 10px; box-sizing: border-box; }

.footer .in { box-shadow: inset 2px 0 0 #f4f4f4, inset -2px 0 0 #f4f4f4; }
.footer__location:before { content: " "; display: inline-block; margin-right: 8px; background-image: url("../img/icons.png"); width: 12px; height: 16px; background-position: -473px -86px; opacity: 0.5; position: relative; top: 2px; }
.footer__logos { text-align: center; padding: 19px 0; border-top: 1px solid #efefef; }
.footer__logos img { vertical-align: middle; margin: 0 5px; }
.footer__logos br { display: none; }
.footer__copy { text-align: center; font-size: 9px; color: #868686; line-height: 1.2; padding-top: 23px; border-top: 1px solid #efefef; padding-bottom: 15px; }
.footer__copy strong { display: block; font-size: 11px; font-family: Georgia, serif; font-style: italic; font-weight: normal; text-align: center; margin-top: 15px; }
.footer__bottom { border-top: 1px solid #EFEFEF; text-align: center; font-size: 9px; color: #868686; line-height: 1.1; padding: 18px 20px 20px 20px; }
.footer__top { *zoom: 1; }
.footer__top:after { content: " "; display: table; clear: both; }
.footer__icons { float: right; width: 242px; min-height: 215px; text-align: center; padding-top: 25px; border-left: 2px solid #fafafa; box-sizing: border-box; }
.footer__icons .footer__iconsimg { background: none; text-align: center; display: inline-block; }
.footer__icons .footer__iconsimg img { vertical-align: middle; margin: 5px 10px; }
.footer__help { width: 217px; border-right: 2px solid #fafafa; min-height: 215px; padding: 24px 0 0 27px; float: left; box-sizing: border-box; }
.footer__help h3 { font-size: 11px; font-weight: 900; color: #000; text-transform: uppercase; margin-bottom: 14px; }
.footer__help ul { list-style: none; color: #bcbcbc; }
.footer__help ul li { margin-bottom: 12px; }
.footer__help ul a { color: #bcbcbc; text-decoration: none; }
.footer__help ul a:hover { color: #000; text-decoration: underline; }
.footer .footer__about { text-align: center; padding-top: 33px; padding-bottom: 20px; }
.footer .footer__about a { font-size: 11px; color: #bcbcbc; margin: 0 6px; text-decoration: none; }
.footer .footer__about a:hover { color: #000; }
.footer .footer__about .footer__logo { background-image: url("../img/icons.png"); width: 208px; height: 17px; background-position: -160px -175px; display: block; margin: 0 auto; margin-bottom: 22px; }

.signup { margin: 26px 0 20px 0; background: #f6f6f6; height: 27px; line-height: normal; border-radius: 27px; position: relative; }
.signup .signup__go { position: absolute; right: 12px; top: 6px; appearance: none; background: transparent; }
.signup .signup__go span { color: #bcbcbc; font-size: 8px; }
.signup__icon { position: absolute; left: 14px; top: 7px; color: #b3b3b3; }
.signup input[type=text] { position: absolute; width: 236px; height: 27px; line-height: normal; font-family: serif; font-size: 11px; font-style: italic; left: 40px; top: 0; background: transparent; }

.l { background: #f0f0f0; }

.post { padding: 50px 40px 40px 54px; }
.post h1 { font-family: Lato, sans-serif; font-weight: 900; font-size: 40px; margin-bottom: 15px; }
.post p { font-size: 14px; line-height: 1.5; color: #858585; margin-bottom: 1em; }
.post p.lead { font-size: 23px; font-family: Lato, sans-serif; font-style: italic; font-weight: 100; line-height: 1.6; }


